/** @define c-page-header */
.c-page-header {
  box-sizing  : border-box;
  display     : flex;
  min-height  : 75vh;
  padding-top : var(--header-height);
  position    : relative;
  width       : 100%;

  @include media("<=tablet") {
    min-height : 100%;
  }

  /* stylelint-disable-next-line */
  .c-media {
    @include z-index(hero-image);
    height   : 100%;
    left     : 0;
    position : absolute;
    top      : 0;
    width    : 100%;
    z-index  : 1;

    /* stylelint-disable-next-line */
    img {
      height     : 100%;
      left       : 0;
      object-fit : cover;
      position   : absolute;
      top        : 0;
      width      : 100%;
    }
  }

  &__overlay {
    @include z-index(hero-overlay);
    /* stylelint-disable-next-line */
    background            : linear-gradient(0deg, rgba(45, 45, 50, 0.35), rgba(45, 45, 50, 0.35));
    background-blend-mode : multiply, normal;
    height                : 100%;
    left                  : 0;
    opacity               : calc(var(--hero-opacity) * 0.01);
    position              : absolute;
    top                   : 0;
    width                 : 100%;
  }

  &__image-placeholder {
    @include z-index(hero-image);
    height     : 100%;
    left       : 0;
    object-fit : cover;
    position   : absolute;
    top        : 0;
    width      : 100%;
  }

  &__container {
    @include grid;
    @include z-index(hero-content);
  }

  &__breadcrumbs {
    grid-column : 1 / -1;
    margin      : 3rem 0;

    @include media("<=desktop") {
      margin-top : 2rem;
    }
  }

  &__text-wrapper {
    align-self     : flex-end;
    color          : $white;
    grid-column    : 1 / 8;
    padding-bottom : var(--space-3xl);
    position       : relative;

    @include media("<=desktop") {
      grid-column : 1 / 9;
    }

    @include media("<=tablet") {
      grid-column    : 1 / -1;
      padding-bottom : 1rem;
    }
  }

  &__title {
    @include ts-heading-1;
  }

  &__description {
    @include ts-heading-6;
    margin-top : var(--space-s);
  }

  &__aside {
    align-self     : flex-end;
    display        : flex;
    flex-direction : column;
    gap            : 3rem;
    grid-column    : 10 / -1;
    padding-bottom : var(--space-3xl);

    @include media("<=desktop") {
      grid-column : 9 / -1;
    }

    @include media("<=tablet") {
      flex-direction : row;
      gap            : 0.5rem;
      grid-column    : 1 / -1;
      padding-bottom : 2.25rem;
    }
  }

  &__language-switcher-links {
    @include media("<=tablet") {
      @include z-index(languages-overlay);
      display  : none;
      left     : 0;
      position : fixed;
      top      : 0;
      width    : 100%;
    }
  }

  &__languages-button {
    align-items     : center;
    background      : $yellow-light;
    border          : none;
    border-radius   : 2rem;
    color           : $primary;
    cursor          : pointer;
    display         : none;
    height          : 2rem;
    justify-content : center;
    transition      : background-color 0.3s;
    width           : 2rem;

    @include media("<=tablet") {
      display : flex;
    }

    &:hover,
    &:focus {
      background-color : $yellow-dark;
    }
  }
}
